module.exports = "/*-------------------------------*/\n/*           Wrappers            */\n/*-------------------------------*/\n#sidebar-wrapper {\n  z-index: 1000;\n  height: 100%;\n  overflow-y: auto;\n  overflow-x: hidden;\n  transition: all 0.5s ease;\n  position: absolute;\n  top: 0px;\n  padding: 0px;\n  background-image: url(\"/assets/images/logobg.png\");\n  background-position-x: center;\n  background-repeat: no-repeat;\n  background-size: cover; }\n#sidebar-wrapper ::-webkit-scrollbar {\n    display: none; }\n/*-------------------------------*/\n/*     Sidebar nav styles        */\n/*-------------------------------*/\n.sidebar-nav {\n  position: absolute;\n  top: 0;\n  width: 100%;\n  margin: 0;\n  padding: 0;\n  list-style: none; }\n.sidebar-nav li {\n    position: relative;\n    line-height: 2rem;\n    display: inline-block;\n    width: 100%; }\n.sidebar-nav li :first-child a {\n      color: #fff;\n      background-color: #1a1a1a; }\n.sidebar-nav li a {\n      display: block;\n      color: #fff;\n      text-decoration: none;\n      padding: 10px 15px 10px 30px; }\n.sidebar-nav li a:hover, .sidebar-nav li a:active, .sidebar-nav li a:focus {\n        color: #8f5a0a;\n        text-decoration: none;\n        background-color: transparent;\n        font-weight: bold; }\n.sidebar-nav li:hover::before {\n      width: 100%;\n      transition: width .2s ease-in; }\n.sidebar-nav li:hover, .sidebar-nav li:active, .sidebar-nav li:focus {\n      color: #3277d3;\n      text-decoration: none;\n      background-color: #ffb94b;\n      background-image: linear-gradient(top, #fddb6f, #ffb94b);\n      color: #8f5a0a; }\n.sidebar-nav .sidebar-brand {\n    height: auto;\n    font-size: 20px;\n    line-height: 44px;\n    background: #fff; }\n.sidebar-nav .sidebar-brand:hover, .sidebar-nav .sidebar-brand:active, .sidebar-nav .sidebar-brand:focus {\n      background-color: #fff; }\n/*-------------------------------*/\n/*       Hamburger-Cross         */\n/*-------------------------------*/\n.hamburger {\n  position: fixed;\n  top: 15px;\n  left: 5px;\n  z-index: 999;\n  display: block;\n  width: 22px;\n  height: 22px;\n  background: transparent;\n  border: none; }\n.hamburger:hover, .hamburger:focus, .hamburger:active {\n    outline: none; }\n.hamburger .hamb-top,\n  .hamburger .hamb-middle,\n  .hamburger .hamb-bottom {\n    position: absolute;\n    left: 0;\n    height: 4px;\n    width: 100%; }\n.hamburger.is-closed:before {\n  content: '';\n  display: block;\n  width: 100px;\n  font-size: 14px;\n  color: #fff;\n  line-height: 32px;\n  text-align: center;\n  opacity: 0;\n  -webkit-transform: translate3d(0, 0, 0);\n  -webkit-transition: all .35s ease-in-out; }\n.hamburger.is-closed:hover .hamb-top {\n  top: 0px;\n  -webkit-transition: all .35s ease-in-out; }\n.hamburger.is-closed:hover .hamb-bottom {\n  bottom: 0;\n  -webkit-transition: all .35s ease-in-out; }\n.hamburger.is-closed:hover:before {\n  opacity: 1;\n  display: block;\n  -webkit-transform: translate3d(-100px, 0, 0);\n  -webkit-transition: all .35s ease-in-out; }\n.hamburger.is-closed .hamb-top,\n.hamburger.is-closed .hamb-middle,\n.hamburger.is-closed .hamb-bottom {\n  background-color: #dadadc; }\n.hamburger.is-closed .hamb-top {\n  top: 2px;\n  -webkit-transition: all .35s ease-in-out; }\n.hamburger.is-closed .hamb-middle {\n  top: 50%;\n  margin-top: -2px; }\n.hamburger.is-closed .hamb-bottom {\n  bottom: 2px;\n  -webkit-transition: all .35s ease-in-out; }\n.hamburger.is-open .hamb-top,\n.hamburger.is-open .hamb-middle,\n.hamburger.is-open .hamb-bottom {\n  background-color: white; }\n.hamburger.is-open:hover .hamb-top {\n  top: 16px;\n  -webkit-transition: all .35s ease-in-out; }\n.hamburger.is-open:hover:before {\n  opacity: 1;\n  display: block;\n  -webkit-transform: translate3d(-100px, 0, 0);\n  -webkit-transition: all .35s ease-in-out; }\n.hamburger.is-open .hamb-top,\n.hamburger.is-open .hamb-bottom {\n  top: 50%;\n  margin-top: -2px; }\n.hamburger.is-open .hamb-top {\n  -webkit-transform: rotate(45deg);\n  -webkit-transition: -webkit-transform 0.2s cubic-bezier(0.73, 1, 0.28, 0.08); }\n.hamburger.is-open .hamb-middle {\n  display: none; }\n.hamburger.is-open .hamb-bottom {\n  -webkit-transform: rotate(-45deg);\n  -webkit-transition: -webkit-transform 0.2s cubic-bezier(0.73, 1, 0.28, 0.08); }\n.hamburger.is-open:before {\n  content: '';\n  display: block;\n  width: 100px;\n  font-size: 14px;\n  color: #fff;\n  line-height: 32px;\n  text-align: center;\n  opacity: 0;\n  -webkit-transform: translate3d(0, 0, 0);\n  -webkit-transition: all .35s ease-in-out; }\n/*-------------------------------*/\n/*            Overlay            */\n/*-------------------------------*/\n.overlay {\n  position: fixed;\n  width: 100%;\n  height: 100%;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: #000000;\n  opacity: 0.8;\n  z-index: 101; }\n"